@import url(https://fonts.googleapis.com/css2?family=Staatliches&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: linear-gradient(
    -225deg,
    #aeea00 60%,
    #aeea00 40%,
    #c6ff00 50%
  );
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

hr {
  border: 0;
  height: 1px;
  background-image: linear-gradient(
    to right,
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0.75),
    rgba(0, 0, 0, 0)
  );
}

.container-box {
  width: 80%;
  background-color: white;
  position: relative;
  z-index: 9;
}

.about-box {
  width: 80%;
  background-color: #fff;
  position: relative;
  z-index: 9;
}

.skills-box {
  width: 80%;
  background-color: transparent;
  position: relative;
  z-index: 9;
}

.exp-section {
  width: 80%;
  background-color: transparent;
  position: relative;
  z-index: 9;
}

.particles-box {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
}

.img-resize {
  width: 8em;
  height: 3em;
}

.style {
  font-size: 20px;
}

.jumbo-style {
  background-color: #212121;
}

.body-title-style {
  font-size: 25px;
  font-weight: 800;
}

.nav-theme {
  background-color: #212121;
  font-size: 20px;
}

.animate-navbar {
  box-shadow: 1px 1px 1px hsla(240, 20%, 8%, 0.973);
  -webkit-animation: moveDown 0.5s ease-in-out;
          animation: moveDown 0.5s ease-in-out;
}

.logo {
  color: #64dd17;
}

@-webkit-keyframes moveDown {
  from {
    transform: translateY(-5rem);
  }
  to {
    transform: translateY(0rem);
  }
}

@keyframes moveDown {
  from {
    transform: translateY(-5rem);
  }
  to {
    transform: translateY(0rem);
  }
}

.custom-img {
  height: 100vh;
  object-fit: cover;
  z-index: 9;
}

.particles-box-car {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

h1 {
    font-family: 'Staatliches', cursive;
}
.about {
  background-color: transparent;
  font-size: 1.1em;
}

.profile {
  max-width: 18em;
}

.my-details {
  background-color: rgba(255, 255, 255, 0.747);
}

.focus {
  transition: transform 0.3s;
}

.focus:hover {
  transform: scale(1.05);
}

.card-title {
  font-size: 2em;
  font-weight: 700;
}

.image-style {
  width: 3em;
}

.font-details {
  font-size: 3em;
  font-weight: 500;
  color: #212121;
}

.font-details-b {
  font-size: 3em;
  font-weight: 500;
  color: #212121;
}

.accordian-main {
  cursor: pointer;
  display: inline-block;
  -webkit-text-decoration: none;
  text-decoration: none;
  font-size: 14px;
  background-color: #212121;
  border: none;
  border-radius: 4px;

  padding: 10px;
  color: #fff;
}

.list-styles {
  list-style-type: circle;
}

.image-style1 {
  width: 3em;
}

.contact-jumbotron {
  background-image: url(/portfolio/static/media/background.f6814621.webp);
  background-size: cover;
  background-repeat: no-repeat;
}

.footer-style {
  background-color: #212121;
  color: #fff;
  font-size: 1em;
  border: 2px solid #212121;
  border-top-left-radius: 3px;
}

